.main_slide {
  position: relative;
  overflow: hidden;
}
.main_slide .slide_img {
  position: relative;
  padding-top: 121.8666666667%;
}
.main_slide .slide_img img {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: auto;
  transform: translateY(-50%);
}
.main_slide .slide_info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 39px 25px 70px;
  color: #fff;
  box-sizing: border-box;
}
.main_slide .slide_info .category {
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
}
.main_slide .slide_info .description {
  font-size: 14px;
  line-height: 21px;
}
.main_slide .slide_info .description .summary {
  display: block;
  padding-bottom: 10px;
  font-size: 30px;
  line-height: 39px;
  font-weight: 500;
}
.main_slide .pagination {
  width: 100px !important;
  bottom: 5px !important;
  z-index: 5;
}
.main_slide .pagination .swiper-pagination-bullet {
  background: rgba(255, 255, 255, 0.5);
  opacity: 1;
  color: transparent;
}
.main_slide .pagination .swiper-pagination-bullet-active {
  background: #fff;
}

.cm_main_tab {
  padding-top: 30px;
}
.cm_main_tab .tab_list {
  display: flex;
  background-color: var(--OldGray2);
  border-radius: 4px;
}
.cm_main_tab .tab_list .tab_menu {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 13px;
  font-weight: 500;
  border-radius: inherit;
  color: var(--OldGray6);
}
.cm_main_tab .tab_list .tab_menu[aria-selected=true] {
  background-color: #fff;
  border: 1px solid var(--OldGray4);
  box-sizing: border-box;
  color: #000;
}

.cm_filter_tab {
  padding: 0 0 15px;
}
.cm_filter_tab .filter_head {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
}
.cm_filter_tab .filter_title {
  font-size: 18px;
  line-height: 27px;
  font-weight: 400;
}
.cm_filter_tab .filter_title:only-child {
  width: 100%;
  text-align: center;
}
.cm_filter_tab .filter_title:only-child .filter_more {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.cm_filter_tab .filter_more {
  float: right;
  position: relative;
  display: inline-block;
  padding: 4.5px 16px 4.5px 10px;
  font-size: 12px;
  line-height: 18px;
  vertical-align: top;
  color: var(--OldGray7);
}
.cm_filter_tab .filter_more:before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 14px;
  height: 14px;
  background-position: -20px -43px;
}
.cm_filter_tab .option_toggle {
  position: relative;
  padding-right: 17px;
  font-size: 14px;
  line-height: 21px;
  color: #000;
}
.cm_filter_tab .option_toggle:before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_main.png") 0 0 no-repeat;
  background-size: 40px 20px;
  width: 12px;
  height: 13px;
  background-position: -28px 0px;
}
.cm_filter_tab .filter_list {
  margin: 0 -15px;
  padding: 10px 15px;
  overflow-x: auto;
  white-space: nowrap;
  font-size: 0;
}
.cm_filter_tab .filter_list::-webkit-scrollbar {
  display: none;
}
.cm_filter_tab .filter_item {
  display: inline-block;
}
.cm_filter_tab .filter_item + .filter_item {
  margin-left: 8px;
}
.cm_filter_tab .filter_btn {
  display: block;
  padding: 4px 10px 3px;
  border-radius: 4px;
  border: 1px solid var(--OldGray4);
  font-size: 14px;
  line-height: 21px;
  color: var(--OldGray6);
}
.cm_filter_tab .filter_btn[aria-selected=true] {
  border-color: #000;
  color: #000;
}

.cm_banner + .cm_filter_tab {
  padding-top: 0;
}

.cm_banner {
  position: relative;
  padding: 60px 0 0;
}
.cm_banner .banner_slide {
  overflow: hidden;
  margin: 0 -15px 0;
  padding: 0 15px 10px;
}
.cm_banner .banner_slide ~ .swiper-pagination {
  position: relative;
  bottom: auto;
  left: auto;
  line-height: 6px;
}
.cm_banner .banner_slide ~ .swiper-pagination .swiper-pagination-bullet {
  vertical-align: top;
}
.cm_banner .cm_banner_card {
