  display: flex;
  align-items: center;
  border-radius: 6px;
  font-size: 0;
}
.cm_banner .banner_thumb,
.cm_banner .banner_info {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
}
.cm_banner .banner_thumb img {
  width: 100%;
}
.cm_banner.type_banner1 .cm_banner_card {
  background: var(--OldGray1);
}
.cm_banner.type_banner1 .banner_thumb {
  overflow: hidden;
  flex: 0 0 135px;
  border-radius: 6px 0 0 6px;
}
.cm_banner.type_banner1 .banner_info {
  overflow: hidden;
  padding: 0 15px;
}
.cm_banner.type_banner1 .banner_info .title {
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;
  color: #000;
  overflow: hidden;
  display: block;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  word-break: break-all;
  word-wrap: break-word;
}
.cm_banner.type_banner1 .banner_info .sub {
  display: flex;
  padding-top: 3px;
  font-size: 12px;
  line-height: 18px;
  color: var(--OldGray7);
}
.cm_banner.type_banner1 .banner_info .sub .item {
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.cm_banner.type_banner1 .banner_info .sub .item + .item {
  flex: 1 0 auto;
  white-space: normal;
  overflow: visible;
  text-overflow: unset;
}
.cm_banner.type_banner1 .banner_info .sub .item + .item:before {
  content: "";
  display: inline-block;
  margin: 0 6px;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: var(--OldGray7);
  vertical-align: 3px;
}
.cm_banner.type_banner2 .banner_slide {
  padding-bottom: 15px;
}
.cm_banner.type_banner2 .cm_banner_card {
  min-height: 80px;
  background: #484848;
}
.cm_banner.type_banner2 .banner_thumb {
  position: absolute;
  right: 0;
  top: 0;
  width: 135px;
}
.cm_banner.type_banner2 .banner_info {
  padding: 0 135px 0 15px;
  width: 100%;
  line-height: 21px;
  font-weight: 500;
}
.cm_banner.type_banner2 .banner_info .title {
  font-size: 15px;
  color: #fff;
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.cm_banner.type_banner2 .banner_info .sub {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.65);
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.goods_ranking .ranking_list {
